.header_box {
    width: 100vw;
    height: 60px;
    position: fixed;
    top: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 2px 10px rgba(109, 109, 109, 0.25), inset 0 1px 0 rgba(255, 255, 255, 0.3);
    background-color: #fff;
    transition: all .5s;
    z-index: 20;
}

.scroll_active {
    opacity: 0;
    transition: all .5s;
}

.header_wrapper {
    margin: 0 25px;
}

.user_info {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.user_name {
    margin: 0 20px;
    font-size: 18px;
    font-weight: 400;
}

.user_info > img {
    width: 40px;
    border: .5px solid rgba(0, 0, 0, 0);
    border-radius: 50px;
    transition: all .5s;
    cursor: pointer;
}

.user_info > img:hover {
    transform: scale(1.2);
    transition: all .5s;
}

.website_logo {
    margin: 0 25px;
}

.website_logo > img {
    width: 100px;
}
