.not_found_wrapper {
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    width: 100vw;

}

.contain_block {
    position: relative;
    max-width: 2000px;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
}

.info_for_back {
    width: 700px;
}

.title_404 {
    position: absolute;
    top: 120px;
    left: 30px;
    text-shadow: 5px 5px rgb(115, 60, 166);
    font-size: 100px;
    font-weight: 800;
    text-transform: uppercase;
    color: rgb(253, 185, 39);
    -webkit-text-stroke: .5px rgb(253, 185, 39);
    cursor: default;
    -webkit-transition: all .5s;
    -moz-transition: all .5s;
    -ms-transition: all .5s;
    -o-transition: all .5s;
    transition: all .5s;
}

.title_404:hover {
    -webkit-text-stroke: .5px black;
    text-shadow: 5px 5px rgba(115, 60, 166, 0);
    color: rgba(0, 0, 0, 0);
    -webkit-transition: all .5s;
    -moz-transition: all .5s;
    -ms-transition: all .5s;
    -o-transition: all .5s;
    transition: all .5s;
}

.block_wrapper {
    color: #777777;
    background-color: rgb(246, 246, 246);
    box-shadow: -5px 5px 15px rgb(253, 185, 39);
    border-radius: 25px;
    height: 200px;
    width: 500px;
    margin: 0 auto;
    font-size: 35px;
    font-weight: 400;
    padding: 0 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    line-height: 1.2em;
    transition: all .5s;
    text-transform: uppercase;
}

.block_wrapper:hover {
    box-shadow: -10px 10px 40px rgb(253, 185, 39);
    -webkit-transform: scale(1.1);
    -moz-transform: scale(1.1);
    -ms-transform: scale(1.1);
    -o-transform: scale(1.1);
    transform: scale(1.1);
    transition: all .5s;
}

.text_wrapper {
    margin: 20px;
}

.go_to_login {
    text-shadow: -2px 2px rgb(115, 60, 166);
    color: rgb(253, 185, 39) !important;
    font-weight: 800 !important;
    font-size: 40px !important;
    transition: all .5s;
}

.go_to_login:hover {
    text-shadow: -3px 3px rgb(115, 60, 166);
    transition: all .5s;
}

.go_to_login:active {
    color: rgb(62, 215, 1) !important;
    text-shadow: -2px 2px 3px rgb(115, 60, 166);
    transition: all .5s;
}

.first_circle {
    position: relative;
    box-shadow: 5px 5px 15px rgb(253, 185, 39);
    width: 460px;
    height: 460px;
    margin-right: 100px;
    background-color: rgb(253, 185, 39);
    border-radius: 50%;
    transition: all .5s;
}

.first_circle:hover {
    transition: all .5s;
    transform: scale(0.7);
    box-shadow: 10px 10px 25px rgb(253, 185, 39);
}

.animation_wrapper {
    position: absolute;
    overflow: hidden;
    box-shadow: 5px 5px 15px #5d5d5d;
    top: 46px;
    left: 31px;
    background-color: rgb(115, 60, 166);
    border-radius: 50%;
    width: 400px;
    height: 400px;
    transition: all .5s;
}

.animation_wrapper:hover {
    transition: all .5s;
    transform: scale(1.5);
    box-shadow: 1px 1px 100px rgb(253, 185, 39);
}